import React from 'react';
import styles from './homeModal.module.css';
import { loginEmail, signupEmail } from '../../fbase';

const HomeModal = ({ openModal, setOpenModal }) => {
  const onClick = (e) => {
    if (e.target.id === 'modalClose') {
      setOpenModal(!openModal);
    }
  };
  return (
    <div id='modalClose' className={styles.container} onClick={onClick}>
      <div className={styles.contents}>
        <button
          className={styles.closeBtn}
          id='modalClose'
          type='button'
          onClick={onClick}
        >
          X
        </button>
        <p className={styles.title}></p>
        <form className={styles.signForm}>
          <input required type='text' placeholder='' />
          <input required type='email' placeholder='()' />
          <input
            required
            minLength='6'
            maxLength='16'
            type='password'
            placeholder='(6~16 )'
          />
          <input
            required
            minLength='6'
            maxLength='16'
            type='password'
            placeholder=' '
          />
          <input
            required
            type='tel'
            placeholder=' (-  )'
          />
          <input
            required
            className={styles.signUpBtn}
            type='submit'
            value=''
          />
        </form>
      </div>
    </div>
  );
};

export default HomeModal;
