import { signInWithPopup } from 'firebase/auth';
import React from 'react';
import { useState } from 'react';
import { googleLoginProvider, authService } from '../fbase';
import AppRouter from './router';
import styles from './app.module.css';

const App = () => {
  const [isLogin, setIsLogin] = useState(false);
  const [userGoogleData, setUserGoogleData] = useState(null);

  const handleGoogleLogin = () => {
    const provider = googleLoginProvider; // provider google 
    signInWithPopup(authService, provider) // popup    
      .then((data) => {
        setUserGoogleData(data.user);
      })
      .catch((error) => {
        console.log(error);
      });
  };
  return (
    <div className={styles.container}>
      <AppRouter
        isLogin={isLogin}
        setIsLogin={setIsLogin}
        handleGoogleLogin={handleGoogleLogin}
      />
    </div>
  );
};

export default App;
